import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getTimbangan } from "../../GetTimbangan";

const renderField = ({
  input,
  label,
  type,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormTambahPembelianRongsok extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    getTimbangan(this.props.change);
  }
  render() {
    return (
      <>
        <div className="col-lg-12">
          <div className="row">
            <div className="col-lg-3">
              <div className="form-group">
                <label htmlFor="">Kode Barang</label>
                <Field
                  name="kodeBarang"
                  component="select"
                  className="form-control"
                >
                  <option value="DEFAULT" key="DEFAULT">
                    SILAHKAN PILIH
                  </option>
                </Field>
              </div>
            </div>
            <div className="col-lg-3">
              <Field
                label="Keterangan"
                name="keteragan"
                component={renderField}
                type="text"
              />
            </div>
            <div className="col-lg-3">
              <Field
                label="Kadar(%)"
                name="kadar"
                component={renderField}
                type="text"
              />
            </div>
            <div className="col-lg-3">
              <Field label="P" name="p" component={renderField} type="text" />
            </div>
            <div className="col-lg-3">
              <Field label="K" name="k" component={renderField} type="text" />
            </div>
            <div className="col-lg-3">
              <Field
                label="Bungkus(Gr)"
                name="bungkus"
                component={renderField}
                type="text"
              />
            </div>
            <div className="col-lg-3">
              <Field
                label="Berat(Gr)"
                name="bruto"
                component={renderField}
                type="text"
              />
            </div>
            <div className="col-lg-3">
              <Field
                label="Harga(%)"
                name="harga"
                component={renderField}
                type="text"
              />
            </div>
            <div className="col-lg-3">
              <Field
                label="Murni(Gr)"
                name="netto"
                component={renderField}
                type="text"
              />
            </div>
            <div className="col-lg-12">
              <div className="text-right">
                <button className="btn btn-primary">Simpan</button>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

FormTambahPembelianRongsok = reduxForm({
  form: "FormTambahPembelianRongsok",
  enableReinitialize: true,
})(FormTambahPembelianRongsok);
export default connect()(FormTambahPembelianRongsok);
