import React, { Component } from "react";
import { connect } from "react-redux";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import SelectSearch from "react-select-search";
import FormTambahPembelianRongsok from "./Components/FormTambahPembelianRongsok";
import { reduxForm } from "redux-form";

const { SearchBar } = Search;
class PembayaranPenjualanLM extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
      step: 0,
      step1: "row",
      step2: "row d-none",
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "namaToko",
          text: "Nama Toko",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "noBon",
          text: "Nomor Bon",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "sales",
          text: "Sales",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "bonRp",
          text: "Bon (Rp)",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "bonGr",
          text: "Bon (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
      ],
      data: [
        {
          tanggal: "2020-11-10",
        },
      ],
      items: [
        {
          value: 0,
          name: "Cobol",
        },
        {
          value: 1,
          name: "JavaScript",
        },
        {
          value: 2,
          name: "Basic",
        },
        {
          value: 3,
          name: "PHP",
        },
        {
          value: 4,
          name: "Java",
        },
      ],
    };
  }
  handleOnSelect = (item) => {
    console.log(item);
  };
  render() {
    return (
      <>
        <div className="container-fluid">
          <div className="card">
            <div className="card-body">
              <div className="col-lg-12">
                <h2>Pembelian Rongsok</h2>
              </div>
              <br />
              <div className="col-lg-12">
                <div className="row">
                  <div className="col-lg-3">
                    <div className="form-group">
                      <label htmlFor="">Tanggal</label>
                      <input
                        name="tanggal"
                        type="date"
                        label="tanggal"
                        className="form-control"
                      />
                    </div>
                  </div>
                  <div className="col-lg-3" style={{ zIndex: 20 }}>
                    <div className="form-group">
                      <label htmlFor="">Nama Toko</label>
                      <SelectSearch
                        options={this.state.items}
                        search
                        placeholder="Select your country"
                      />
                    </div>
                  </div>
                  <div className="col-lg-3">
                    <div className="form-group">
                      <label htmlFor="">Nomor Bon</label>
                      <input
                        name="noBon"
                        type="text"
                        label="noBon"
                        className="form-control"
                      />
                    </div>
                  </div>
                  <div className="col-lg-3">
                    <div className="form-group">
                      <label htmlFor="">Detail</label>
                      <input
                        name="detail"
                        type="text"
                        label="detail"
                        className="form-control"
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-lg-12">
                <br />
              </div>
              <div className="col-lg-12">
                <div className="row">
                  <div className="col-lg-2">
                    <div className="form-group">
                      <label htmlFor="">Total NW</label>
                      <input
                        name="totalNW"
                        label="totalNW"
                        type="text"
                        className="form-control"
                        readOnly={true}
                      />
                    </div>
                  </div>
                  <div className="col-lg-2">
                    <div className="form-group">
                      <label htmlFor="">Total NW Ext</label>
                      <input
                        name="totalNWExt"
                        label="totalNWExt"
                        type="text"
                        className="form-control"
                      />
                    </div>
                  </div>
                  <div className="col-lg-2"></div>
                  <div className="col-lg-2">
                    <div className="form-group">
                      <label htmlFor="">Harga (Rp)</label>
                      <input
                        name="hargaRp"
                        label="hargaRp"
                        type="text"
                        className="form-control"
                      />
                    </div>
                  </div>
                  <div className="col-lg-2">
                    <div className="form-group">
                      <label htmlFor="">Total(Rp)</label>
                      <input
                        name="totalRp"
                        label="totalRp"
                        type="text"
                        className="form-control"
                        readOnly={true}
                      />
                    </div>
                  </div>
                  <div className="col-lg-2">
                    <div className="form-group">
                      <label htmlFor="">Total Rp Ext</label>
                      <input
                        name="totalRpExt"
                        label="totalRpExt"
                        type="text"
                        className="form-control"
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-lg-12 mt-3">
                <div className="text-right">
                  <button
                    className="btn btn-primary"
                    data-toggle="modal"
                    data-target="#tambahModal"
                  >
                    Tambah Barang
                  </button>
                </div>
              </div>
              <div className="col-lg-12">
                <ToolkitProvider
                  keyField="kodeKelompok"
                  data={this.state.data}
                  columns={this.state.columns}
                  search
                >
                  {(props) => (
                    <div className="form-group">
                      <div className="text-right mt-5 mb-3">
                        <SearchBar
                          {...props.searchProps}
                          className="form-control"
                          placeholder="Tuliskan Kode / Nama"
                          style={{ margin: 0, top: "50%" }}
                        />
                      </div>
                      <BootstrapTable
                        {...props.baseProps}
                        striped
                        hover
                        condensed
                        bordered={true}
                        headerClasses="header-table"
                        pagination={paginationFactory()}
                      />
                    </div>
                  )}
                </ToolkitProvider>
              </div>
            </div>
            <div
              id="tambahModal"
              className="modal fade"
              tabIndex="-1"
              role="dialog"
              aria-labelledby="primary-header-modalLabel"
              aria-hidden="true"
            >
              <div className="modal-dialog modal-lg">
                <div className="modal-content">
                  <div className="modal-header modal-colored-header bg-primary">
                    <h4 className="modal-title" id="primary-header-modalLabel">
                      Tambah Pembelian Rongsok
                    </h4>
                    <button
                      id="dismiss_modal"
                      name="dismiss_modal"
                      type="button"
                      className="close"
                      data-dismiss="modal"
                      aria-hidden="true"
                    >
                      ×
                    </button>
                  </div>
                  <div className="modal-body">
                    <FormTambahPembelianRongsok />
                  </div>
                  <div className="modal-footer">
                    <button
                      type="button"
                      className="btn btn-light"
                      data-dismiss="modal"
                      id="close_modal"
                    >
                      Close
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

PembayaranPenjualanLM = reduxForm({
  form: "PembayaranPenjualanLM",
  enableReinitialize: true,
})(PembayaranPenjualanLM);
export default connect()(PembayaranPenjualanLM);
